import React from 'react';
import { Layout, Wrapper, CardHeader } from '../components';
import Helmet from 'react-helmet';
import { CardContent } from '@material-ui/core';
import styled from 'styled-components';
import { graphql } from 'gatsby';

const HTMLTitle = '使用PINKX的剧组';

interface Props {
  data: {
    allHistoryYaml: any;
  };
}

interface States {
  allHistory: [];
  title: string;
}

const Article = styled.article`
  a {
    font-size: 14px;
    margin: 16px 0;
    display: block;
  }
`;

export default class ChangeLogPage extends React.Component<Props, States> {
  constructor(props: any) {
    super(props);
    this.state = {
      allHistory: this.props.data.allHistoryYaml.edges,
      title: HTMLTitle,
    };
  }

  getLocalTime(ns: any) {
    return new Date(ns).toLocaleDateString().replace(/:\d{1,2}$/, '');
  }

  public render() {
    const { title, allHistory } = this.state;
    return (
      <Layout>
        <Helmet title={title} />
        <Wrapper>
          <CardHeader>
            <h2>{title}</h2>
          </CardHeader>
          <CardContent>
            <Article>
              {!!allHistory &&
                allHistory.map((t: any) => {
                  return (
                    <a key={t.node.id} href={t.node.url} target="_blank">
                      🔗 {t.node.title}
                    </a>
                  );
                })}
            </Article>
          </CardContent>
        </Wrapper>
      </Layout>
    );
  }
}

export const AllHistoryQuery = graphql`
  query {
    allHistoryYaml(sort: { order: [DESC], fields: [date] }) {
      edges {
        node {
          id
          title
          url
        }
      }
    }
  }
`;
